import React, { useEffect, useState } from 'react';
import axios from 'axios';
import SingleBlogCard from '../components/SingleBlogCard';
import { Link } from 'react-router-dom';

const UserBlogs = () => {
    const [blogs, setBlogs] = useState([]);

    useEffect(() => {
        fetchBlogs();
    }, []);

    const fetchBlogs = async () => {
        const response = await axios.get('/v1/api/blogs/me', {
            headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
        });
        setBlogs(response.data);
    };

    return (
        <div style={styles.container}>
            <h2 style={styles.title}>My Blogs</h2>
            <Link to="/post-blog" style={styles.link}>Post a New Blog</Link>
            <div style={styles.blogsContainer}>
                {blogs.map(blog => (
                    <SingleBlogCard key={blog._id} blog={blog} />
                ))}
            </div>
        </div>
    );
};

const styles = {
    container: {
        padding: '20px',
    },
    title: {
        color: '#8A2BE2',
    },
    link: {
        color: '#8A2BE2',
        textDecoration: 'none',
        fontSize: '18px',
    },
    blogsContainer: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
    }
};

export default UserBlogs;