import React from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';

const DeleteBlog = () => {
    const { id } = useParams();
    const navigate = useNavigate();

    const handleDelete = async () => {
        await axios.delete(`/v1/api/blogs/${id}`, {
            headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
        });
        navigate('/user-blogs');
    };

    return (
        <div style={styles.container}>
            <h2 style={styles.title}>Are you sure you want to delete this blog?</h2>
            <button onClick={handleDelete} style={styles.button}>Delete</button>
            <button onClick={() => navigate('/user-blogs')} style={styles.button}>Cancel</button>
        </div>
    );
};

const styles = {
    container: {
        padding: '20px',
        textAlign: 'center',
    },
    title: {
        color: '#8A2BE2',
    },
    button: {
        padding: '10px',
        margin: '10px',
        backgroundColor: '#8A2BE2',
        color: '#FFFFFF',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
    }
};

export default DeleteBlog;