import React from 'react';
import { Link } from 'react-router-dom';

const SingleBlogCard = ({ blog }) => {
    return (
        <div style={styles.card}>
            <img src={blog.photo} alt={blog.title} style={styles.image} />
            <h2 style={styles.title}>{blog.title}</h2>
            <p style={styles.author}>By: {blog.author}</p>
            <p style={styles.body}>{blog.body.substring(0, 100)}...</p>
            <Link to={`/blog/${blog._id}`} style={styles.readMore}>Read More</Link>
        </div>
    );
};

const styles = {
    card: {
        backgroundColor: '#E6E6FA',
        padding: '20px',
        borderRadius: '10px',
        margin: '10px',
        width: '300px',
        textAlign: 'center',
    },
    image: {
        width: '100%',
        height: '200px',
        borderRadius: '10px',
    },
    title: {
        color: '#8A2BE2',
        fontSize: '24px',
    },
    author: {
        color: '#9370DB',
        fontSize: '18px',
    },
    body: {
        color: '#4B0082',
        fontSize: '16px',
    },
    readMore: {
        color: '#8A2BE2',
        textDecoration: 'none',
        fontSize: '16px',
    }
};

export default SingleBlogCard;